import {AfterViewInit, Component, Input, OnDestroy, OnInit} from '@angular/core';
import {SharedServiceService} from '../../shared-service.service';
import {Location} from '@angular/common';
import {NavController} from '@ionic/angular';
import {Router, RoutesRecognized} from '@angular/router';
import {filter, pairwise} from 'rxjs/operators';
import {StorageService} from '../storage.service';
import {appConstants} from '../../../assets/constants/app-constants';

@Component({
  selector: 'sandhyadeep-header',
  templateUrl: './header-component.page.html',
  styleUrls: ['./header-component.page.scss'],
})
export class HeaderComponentPage implements OnInit, AfterViewInit {
  hideProfileIcon: any = false;
  previousUrl: any = '';
  searchTerm: any = '';
  showSearchBox: any = false;
  @Input() showBackIcon: any = false;
  cartDetails: any = [];
  constructor(private storageService: StorageService, private router: Router, private navCtrl: NavController, private sharedService: SharedServiceService, private location: Location) { }

  setFilteredItems(event) {
    console.log(event);
  }

  ngOnInit(): void {
    this.sharedService.onSettingEvent.subscribe((res) => {
      this.hideProfileIcon = res;
    });

    this.sharedService.showSearchBox.subscribe((res) => {
      this.showSearchBox = res;
      this.showBackIcon = res;
    });

    this.sharedService.showBackIcon.subscribe((res) => {
      this.showBackIcon = res;
    });

    this.storageService.getStoredValue(appConstants.SELECTED_SERVICES).then((val) => {
      this.cartDetails = val;
    });
  }

  ngAfterViewInit(): void {
    this.sharedService.updateCart.subscribe((res) => {
      this.cartDetails = res;
    });
  }

  ionViewWillEnter() {

    this.sharedService.onSettingEvent.subscribe((res) => {
      this.hideProfileIcon = res;
    });

    this.sharedService.showSearchBox.subscribe((res) => {
      this.showSearchBox = res;
      this.hideProfileIcon = !res;
    });
  }

  backClicked() {
    // this.navCtrl.back();
    this.router.navigate(['admin-panel']);
  }

}
